@import '../../styles/mixins';

$large-size: 1320px;
$medium-size: 1024px;
$small-size: 768px;
$smallest-size: 480px;

$font-size-base: 14;
$line-heaight-base: 17px;
$font-family-base: 'Roboto', sans-serif;
$font-family-solid: 'Montserrat', sans-serif;
$font-family-header: 'Fugaz One', cursive;
$font-family-text: 'Open Sans', sans-serif;

$primary-1: #ae0032;
$primary-2: #455162;
$primary-3: #1f2f46;

$neutral-1: #0d1720;
$neutral-2: #636363;
$neutral-3: #dadada;
$neutral-4: #f2f2f2;
$neutral-5: #fcfbfa;

$gray-1: #F1F1F1;
$gray-2: #404040;
$gray-3: #292929;
$gray-4: #333C30;
$gray-5: #F8F8F8;
$gray-6: #D4D4D4;

$orange: #EB5B1F;
$orange-1:#B44619;
$orange-hover:#DC6331;

$green-1: #1B3A1B;

.company {
  background-color: $gray-5;
}

.company-wrapper {
  display: flex;
  justify-content: space-between;
  padding-top: 120px;
  @include phone {
    flex-wrap: wrap;
    padding-top: 60px;
  }
}

.company-header {
  font-family: $font-family-header;
  font-size: 34px;
  line-height: 42px;
  color: $gray-3;
  text-transform: uppercase;
  margin-bottom: 16px;
  padding-right: 120px;
  @include phone {
    font-size: 24px;
    line-height: 34px;
    padding-right: 0;
  }
}

.company-caption__large {
  font-family: $font-family-text;
  font-weight: 600;
  font-size: 18px;
  line-height: 28px;
  color: $gray-3;
  padding-right: 120px; 
  @include phone {
    font-size: 16px;
    line-height: 26px;
    padding-right: 0;
    margin-bottom: 16px;
  }
}

.company-caption {
  font-size: 16px;
  line-height: 26px;
  color: $gray-3;
  margin-bottom: 32px;
  @include phone {
    margin-bottom: 40px;
  }
}

.company-link {
  font-family: $font-family-text;
  text-decoration: none;
  font-weight: bold;
  font-size: 16px;
  line-height: 22px;
  color: $orange;
  padding-bottom: 6px;
  position: relative;
  margin-bottom: 7px;
  display: inline-block;
  &::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: $orange;
  }
  &:hover {
    color: $gray-4;
    &::after {
      background-color: $gray-4;
    }
  }
}